/* montserrat-regular - latin_cyrillic */
@font-face {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/montserrat-v15-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/montserrat-v15-latin_cyrillic-regular.woff") format("woff");
}
